.section-title {
  margin-bottom: 48px;

  font-size: 32px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: -1.5px;
  text-align: center;




  @include on-tablet {
    font-size: 48px;
  }

  @include on-desktop {
    font-size: 64px;
    margin-bottom: 56px;
  }

  &--align--left{
    text-align: left;
  }
}
